@import url("https://fonts.googleapis.com/css?family=Open+Sans") ;

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background: -webkit-linear-gradient(15deg, #FF5EDF 0%, #04C8DE 100%) ;
  background: -o-linear-gradient(15deg, #FF5EDF 0%, #04C8DE 100%) ;
  background: linear-gradient(75deg, #FF5EDF 0%, #04C8DE 100%) ;
  height: 100% ;
  background-attachment: fixed ;
}

html
{
	height : 100% ;
}

button
{
	cursor: pointer ;
}

.up , .rank
{	color: white ;	
	text-align: center;
}

.up
{	font-size: 1.5rem ;
	font-weight: 300;	}

.rank
{	font-size: 3rem ;	}

.particle
{
	position: fixed;
	left: 0;
	right: 0 ;
	top : 0 ;
	bottom: 0 ;
	z-index: -1;
}

.centre
{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}


